import React, { useState } from 'react';
import { observer } from 'mobx-react-lite';

import './selectionInfo.scss';
import './../../styles/index.scss';

import selectedItemImg from './../../assets/images/coach.png';
import add from './../../assets/images/blue-add.svg';
import reduce from './../../assets/images/blue-reduce.svg';
import { Link } from 'react-router-dom/cjs/react-router-dom.min';



const SelectionInfo = observer(({selectedItem}) => {
    const [isSelected, setSelected] = useState(selectedItem);   //—Å—Ç—ç–π—Ç –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –∞–π—Ç–µ–º–∞ –º–µ–±–µ–ª–∏


    return (
        <div>
        {isSelected === selectedItem
            ? 
            <div className='info__empty'>
                <p>–í—ã –Ω–µ –≤—ã–±—Ä–∞–ª–∏ –ø–æ–∫–∞ –Ω–∏ –æ–¥–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞.</p>
            </div>
            :
            <div className='info'>
                <div className='info__selected-item'>
                    <img src={selectedItemImg} alt='/' />
                    <p>{selectedItem.name}</p>
                </div>


                <div className='info__quantity'>
                    <p>–ö–æ–ª-–≤–æ:</p>
                    <div className='info__quantity-btns'>
                        <button type='button'><img src={reduce} alt='-' /></button>
                        <p className='info__quantity-amount'>1</p>
                        <button type='button'><img src={add} alt='+' /></button>
                    </div>
                </div>

                <div className='info__specifications'>
                    <div className='info__specifications-item'><p>25 –º3</p></div>
                    <div className='info__specifications-item'><p className='info__placeholder' placeholder='–û–±—â–∞—è –º–∞—Å—Å–∞ –Ω–µ—Ç—Ç–æ, –∫–≥'></p></div>
                    <div className='info__specifications-item'><p className='info__placeholder'>–û–±—â–∞—è –º–∞—Å—Å–∞ –±—Ä—É—Ç—Ç–æ, –∫–≥</p></div>
                    <div className='info__specifications-item'><p className='info__placeholder'>–°—Ç–æ–∏–º–æ—Å—Ç—å –æ–¥–Ω–æ–π –µ–¥–∏–Ω–∏—Ü—ã</p></div>
                </div>

                <form>
                    <button className='btn btn__blue btn__submit'>–°–±—Ä–æ—Å–∏—Ç—å</button>
                    <Link to="/selected">
                        <button onClick={e => e.preventDefault} className='btn btn__blue btn__submit'>–î–æ–±–∞–≤–∏—Ç—å</button>
                    </Link>
                </form>
            </div>
        }
        </div>
    )
})

export default SelectionInfo;