import React, { useContext, useState } from 'react';
import SelectionInfo from '../components/SelectionInfo/SelectionInfo';

import './../styles/selectionPage.scss';
import './../styles/index.scss';

import coach from './../assets/images/coach.png';
import { Context } from '..';
import { observer } from 'mobx-react-lite';


const SelectionPage = observer(() => {
    const {furniture} = useContext(Context);
    const [selectedItem, setSelectedItem] = useState('');
    console.log(selectedItem);


    const select = (item) => {
        setSelectedItem(item);      //–¥–æ–±–∞–≤–ª—è–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π –∞–π—Ç–µ–º –≤ —Å—Ç–µ–π—Ç –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è
        furniture.setSelectedFurniture(item);     //–¥–æ–±–∞–≤–ª—è–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π –∞–π—Ç–µ–º –≤ –≥–ª–æ–±–∞–ª—å–Ω–æ–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ
    }


    return (
        <div className='selection'>
            
            <div className='selection__header'>
                <p>–í—ã–±–µ—Ä–∏—Ç–µ –º–µ–±–µ–ª—å, –∫–æ—Ç–æ—Ä—É—é –Ω—É–∂–Ω–æ<br></br>–ø–µ—Ä–µ–≤–µ–∑—Ç–∏</p>
                <p>–ó–∞—Ç–µ–º –∑–∞–ø–æ–ª–Ω–∏—Ç–µ —Å–ª–µ–¥—É—é—â–∏–µ<br></br>–ø–æ–ª—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞:</p>
            </div>

            <form className='selection__search'>
                <input className='selection__search-input' placeholder='–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ' type='text'></input>
                <button className='btn btn__search btn__blue' type='submit'>–ü–æ–∏—Å–∫</button>
            </form>


            {/* –í—ã–≤–æ–¥ –≤—Å–µ–π –º–µ–±–µ–ª–∏ */}
            <div className='selection__wrap'>
                <div className='selection__items'>
                    {furniture.furniture.map(item => {
                        return (
                        <div key={item.id} className='selection__items-item'>
                            <img src={coach} alt="–¥–∏–≤–∞–Ω" />
                            <p className='item_name'>{item.name}</p>
                            <button onClick={() => select(item)} type='button' className='btn btn__blue btn__medium btn__white-active'>–í—ã–±—Ä–∞—Ç—å</button>
                        </div>
                        )
                    })}
                </div>
                

                {/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –≤—ã–±—Ä–∞–Ω–Ω–æ–º —ç–ª–µ–º–µ–Ω—Ç–µ –º–µ–±–µ–ª–∏ */}
                <SelectionInfo selectedItem={selectedItem}/>
            </div>
        </div>
    )
})

export default SelectionPage;
