import React, { useState, useEffect } from 'react';
import './contactForm.scss';
import './../../styles/index.scss';

const ContactForm = () => {
    const [name, setName] = useState('');
    const [mail, setMail] = useState('');
    const [phone, setPhone] = useState('');

    const [error, setError] = useState('');

    const [formValid, setFormValid] = useState(false);
    

    useEffect(() => {
        if(error) {
            setFormValid(false);
        } else {
            setFormValid(true);
        }
    }, [error]);


    //–ü—Ä–æ–≤–µ—Ä–∫–∞ –∏–Ω–ø—É—Ç–∞ —Å –∏–º–µ–Ω–µ–º:
    const inputHandlerName = (e) => {
        if (!e.target.value) {
            setError('* –í–≤–µ–¥–∏—Ç–µ –∏–º—è');
        } else {
            setError('');
        }
        setName(e.target.value);
    }

    //–ü—Ä–æ–≤–µ—Ä–∫–∞ –∏–Ω–ø—É—Ç–∞ —Å –ø–æ—á—Ç–æ–π:
    const inputHandlerMail = (e) => {
        if (e.target.value.length < 5) {
            setError('* –í–≤–µ–¥–∏—Ç–µ –ø–æ—á—Ç—É');
        } else {
            setError ('');
        }
        setMail(e.target.value.toLowerCase());
    }

    //–ü—Ä–æ–≤–µ—Ä–∫–∞ –∏–Ω–ø—É—Ç–∞ —Å —Ç–µ–ª–µ—Ñ–æ–Ω–æ–º:
    const inputHandlerPhone = (e) => {
        if(e.target.value.length < 6) {
            setError('* –í–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞');
        }
        if(e.target.value.length > 12) {
            setError('* –í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –Ω–æ–º–µ—Ä');
        }
        setPhone(e.target.value);
        setError('');
    }


    return (
        <form className='contact-form'>
            <div className='contact-form__inputs'>
                <input value={name} onChange={e => inputHandlerName(e)} type='text' placeholder='–ò–º—è –∏ —Ñ–∞–º–∏–ª–∏—è'></input>
                <input value={mail} onChange={e => inputHandlerMail(e)} type='text' placeholder='–ü–æ—á—Ç–∞'></input>
                <input value={phone} onChange={e => inputHandlerPhone(e)} type='tel' placeholder='–¢–µ–ª–µ—Ñ–æ–Ω'></input>
            </div>
            <p style={{color:'red'}}>{error}</p>

            <div className='contact-form__textarea'>
                <p>–û–ø–∏—à–∏—Ç–µ –≤–∞—à –∑–∞–ø—Ä–æ—Å</p>
                <textarea></textarea>
                <button disabled={!formValid} className='btn btn__whide btn__blue' type='submit'>–°–≤—è–∑–∞—Ç—å—Å—è –ø–æ –¥–æ—Å—Ç–∞–≤–∫–µ</button>
                <p>–ù–∞–∂–∏–º–∞—è –Ω–∞ –∫–Ω–æ–ø–∫—É, –≤—ã –¥–∞–µ—Ç–µ <strong>—Å–æ–≥–ª–∞—Å–∏–µ –Ω–∞ –æ–±—Ä–∞–±–æ—Ç–∫—É</strong><br></br>—Å–≤–æ–∏—Ö –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö</p>
            </div>
        </form>
    )
}

export default ContactForm;
